.navBar {
    display: flex;
    width: 100%;
    justify-content: space-between;
    padding-right: 1rem;
    padding-left: 1rem;
    align-items: center;

    .navList {
        width: 30vw;
        height: 24px;
        list-style-type: none;
        display: flex;
        flex-direction: row;
        justify-content: space-between;
        align-items: center;
        margin: 0;
        
        
    
        img {
            padding: 0;
            margin: 0;
        }
    }

}





